* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.list {
  margin-top: 40px;
  text-align: center;

  h1 {
    font-size: 24px;
    line-height: 28px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 80% 1fr;


    input {
      width: 100%;
      padding: 10px 0;
    }

    button {
      width: 100%;
      padding: 10px 0;
    }

    ul {
      text-align: left;
      display: grid;
      grid-column: 1 /-1;
      li {
        border: 1px solid #000;
        list-style: none;
        display: flex;
        justify-content: space-between;
        background: #eee;
        padding: 15px 0;

        &:nth-child(odd){
          background: #f9f9f9;
        }

        span {
          cursor: pointer;
          padding-right: 10px;
        }
      }
    }
  }
}
